'use strict'

const test = require('ava')

const utils = require('../lib/utils')

test('extracting hastags from text', t => {
  let tags = utils.extractTags('a #pictures with tags #AwEsOmE #Newgram #AVA and #100 ##yes')
  t.deepEqual(tags, ['pictures', 'awesome', 'newgram', 'ava', '100', 'yes'])

  tags = utils.extractTags('a picture with no tags')
  Test.t.deepEqual(tags, [])

  tags = utils.extractTags()
  Test.t.deepEqual(tags, [])

  tags = utils.extractTags(null)
  Test.t.deepEqual(tags, [])
})
